import React, { Component } from 'react'
import DefaultImgae from './../../assets/images/reviews-avatar.jpg';
import './style.css';
import {Link} from 'react-router';

class MyProfile extends Component {
    render () {
        return (
<div>

			<div id="titlebar">
			<div className="row">
				<div className="col-md-12">
					<h2>My Profile</h2>
				
					<nav id="breadcrumbs">
						<ul>
							<li><a href="#">Home</a></li>
							<li><Link to="/dashboard">Dashboard</Link></li>
							<li>My Profile</li>
						</ul>
					</nav>
				</div>
			</div>
		</div>
            <div className="row">
			<div className="col-md-12">
			<div id="add-listing ">

			<div class="add-listing-section myprofilepage">
						<div class="add-listing-headline">
							<h3><i class="sl sl-icon-doc"></i> Profile Informations</h3>
						</div>

					
						<div class="row with-forms">

								<div className="row">
								<div className="col-md-6">
								<label>First Name</label>
								<input type="text" name='txtfirstname' id='txtfirstname'/>
								</div>
								<div className="col-md-6">
								<label>Last Name</label>
								<input  type="text" id='txtxlatsname' name='txtxlatsname'/>
								</div>
								</div>

			<div className="row">
			<div className="col-md-6">
			<label>Email </label>
			<input type="text" name='txtemail' id='txtemail'/>
			</div>
			<div className="col-md-6">
			<label>Mobile</label>
			<input  type="text" id='txtxmobile' name='txtxmobile'/>
			</div>
			</div>

			<div className="row">
			<div className="col-md-12">
			<label>Description </label>
			<textarea name='txtdescription' id='txtdescription'></textarea>
			</div>

			</div>

<div className="row">
			<div className="col-md-6">
			<label><i className="fa fa-facebook-square"></i> Facebook</label>
			<input type="text" name='txtfacebook' id='txtfacebook'/>
			</div>
			<div className="col-md-6">
			<label><i className="fa fa-instagram"></i> Instagram</label>
			<input  type="text" id='txtxinstagram' name='txtxinstagram'/>
			</div>
</div>

<div className="row">
			<div className="col-md-12">
			<label>Address </label>
			<textarea name='txtaddress' id='txtaddress'></textarea>
			</div>
		
</div>
<div className="row">
<div className="col-md-4">
			<label>Country </label>
               <select className="chosen-select-no-single" name='drdcountry' id='drdcountry'>
				   <option>India</option>
				   <option>Uk</option>
				   <option>Usa</option>
			   </select>

			</div>
			<div className="col-md-4">
			<label>State </label>
			<select className="chosen-select-no-single" name='drdstate' id='drdstate'>
				   <option>Gujarat</option>
				   <option>Mp</option>
				   <option>Up</option>
			   </select>
			</div>
			<div className="col-md-4">
			<label>City </label>
			<select className="chosen-select-no-single" name='drdcity' id='drdcity'>
				   <option>Ahmedabad</option>
				   <option>Baroda</option>
			   </select>
			</div>
</div>

						</div>

					</div>



					<div className="add-listing-section margin-top-45">

					
						<div className="add-listing-headline">
							<h3><i className="sl sl-icon-picture"></i> Gallery</h3>
						</div>

<div className="ow with-forms">
<div className="row">

		<div className="col-md-6">
		<div className="edit-profile-photo">
		<img src={DefaultImgae} alt=""/>
		<div className="change-photo-btn">
			<div className="photoUpload">
				<span><i className="fa fa-upload"></i> Upload Profile Image</span>
				<input type="file" className="upload"/>
			</div>
		</div>
	</div>
		</div>

		<div className="col-md-6">
		<div className="edit-profile-photo">
		<img src={DefaultImgae} alt=""/>
		<div className="change-photo-btn">
			<div className="photoUpload">
				<span><i className="fa fa-upload"></i> Upload Cover Image</span>
				<input type="file" className="upload"/>
			</div>
		</div>
	</div>
		</div>


</div>
</div>
					
				

					</div>	




					<button className="button margin-top-15">Save Changes</button>
			</div>
			<div style={{marginTop:'50px'}}></div>
			</div>
            </div>
			</div>
        )
    }
}

export default MyProfile


