import React, { Component } from 'react'
import {Link} from 'react-router';

class Bank extends Component {
    render () {
        return (
			<div>

<div id="titlebar">
			<div className="row">
				<div className="col-md-12">
					<h2>Bank</h2>
				
					<nav id="breadcrumbs">
						<ul>
						<li><a href="#">Home</a></li>
						<li><Link to="/dashboard">Dashboard</Link></li>
							<li>Bank</li>
						</ul>
					</nav>
				</div>
			</div>
		</div>
            <div className="row">
			<div className="col-md-12">
			<div id="add-listing ">

            <div class="add-listing-section myprofilepage">
						<div class="add-listing-headline">
							<h3><i class="sl sl-icon-doc"></i> Bank Informations</h3>
						</div>

                        
						<div class="row with-forms">

								<div className="row">
								<div className="col-md-6">
								<label>Account Holder Name</label>
								<input type="text" name='txtholdername' id='txtholdername'/>
								</div>
								<div className="col-md-6">
								<label>Account Number</label>
								<input  type="text" id='txtxaccnumber' name='txtxaccnumber'/>
								</div>
								</div>
                                </div>

                                <div class="row with-forms">

								<div className="row">
								<div className="col-md-6">
								<label>Bank Name</label>
								<input type="text" name='txtbankname' id='txtbankname'/>
								</div>
								<div className="col-md-6">
								<label>Ifsc Number</label>
								<input  type="text" id='txtifsc' name='txtifsc'/>
								</div>
								</div>
                                </div>
                                <button className="button margin-top-15">Save Changes</button>
                                <div style={{marginTop:'50px'}}></div>

<div className="row">
<div className="col-md-12">
<table class="basic-table">

				<tbody>
                    <tr>
					<th>Account Holder Name</th>
					<th>Account Number</th>
                    <th>Bank Name</th>
                    <th>Ifsc Number</th>
                    <th>Status</th>
				</tr>

				<tr>
					<td data-label="Column 1">Account Holder Name1</td>
					<td data-label="Column 2">Account Number</td>
                    <td data-label="Column 2">Bank Name</td>
                    <td data-label="Column 2">Ifsc Number</td>
                    <td data-label="Column 2">Primary</td>
				</tr>

				<tr>
                <td data-label="Column 1">Account Holder Name1</td>
                <td data-label="Column 2">Account Number</td>
                <td data-label="Column 2">Bank Name</td>
                <td data-label="Column 2">Ifsc Number</td>
                <td data-label="Column 2"></td>
				</tr>

				<tr>
                <td data-label="Column 1">Account Holder Name1</td>
                <td data-label="Column 2">Account Number</td>
                <td data-label="Column 2">Bank Name</td>
                <td data-label="Column 2">Ifsc Number</td>
                <td data-label="Column 2"></td>
				</tr>

				<tr>
                <td data-label="Column 1">Account Holder Name1</td>
                <td data-label="Column 2">Account Number</td>
                <td data-label="Column 2">Bank Name</td>
                <td data-label="Column 2">Ifsc Number</td>
                <td data-label="Column 2"></td>
				</tr>
			</tbody></table>
</div>
</div>



                            </div>
                            <div style={{marginTop:'50px'}}></div>
                            </div>
                            </div>
                            </div>
							</div>

        )
    }
}

export default Bank


