import React, { Component } from 'react'

class TextBox extends Component {
    render () {
        return (
             <input type={this.props.txtType} id={this.props.txtId}  placeholder={this.props.txtPlace} autoComplete="off" className="form-control"  maxLength={this.props.txtMaxLength} ref={this.props.inputRef}/>

            //  <input id="txt_fname" type="name" placeholder="First Name" name="txt_fname" autoComplete="off" className="form-control"  maxLength="70"/>
        )
    }
}
TextBox.defaultProps=
{

    txtType:"text",
    txtId:"",
    txtPlace:"Enter value",
    txtMaxLength:"50",
    inputRef:''

}
export default TextBox


